if (typeof module === 'object' && module.exports) {
  var CsvView = require('./CsvView')
}

function ExcelView (allIssues) {
  this.allIssues = allIssues
}

ExcelView.prototype.fieldValue = CsvView.fieldValueFn(function excelUserFieldFormatter (field, options) {
  var hostBaseUrl = options.hostBaseUrl || ''
  var name = field.displayName || field.name || field.accountId
  return "<a href='" + hostBaseUrl + '/people/' + field.accountId + "' title='" + field.emailAddress + "'>" + name + '</a>'
}, function excelKeyFieldFormatter (field, options) {
  var hostBaseUrl = options.hostBaseUrl || ''
  var name = field.hasOwnProperty('fields') && field.fields.hasOwnProperty('summary') ? field.fields.summary : ''
  name = name.replace(/'/, '&#39;')
  return "<a href='" + hostBaseUrl + '/browse/' + field.key + "' title='" + name + "'>" + field.key + '</a>'
}, function excelTimeFormatter (value, options) {
  return DrawHelper.formatDecimal(value / options.periodDuration, options.jiraConfig.maxFractionDigits)
})

ExcelView.prototype.enquote = function (value) {
  if (!value) return ''
  value = '' + value
  return ('=+-@'.indexOf(value.substring(0, 1)) === -1 ? value : "'" + value).replace(/\r?\n/g, ' ')
}
/*
    Provides raw worklogs.

    Supported options: pivotTableType, monthView, startDate, endDate, user
    TODO? groups, sumSubTask
*/
ExcelView.prototype.generate = function (options) {
  var maxFractionDigits = options.jiraConfig.maxFractionDigits
  var hostBaseUrl = options.hostBaseUrl || ''
  var prepareLink = function (url, text) {
    return "<a href='" + hostBaseUrl + url + "'>" + text + '</a>'
  }
  var pivotTable = PivotTableFactory.createPivotTable(options)
  var isTimeView = ['TimeTracking', 'TimeBalance'].indexOf(options.pivotTableType) >= 0
  var timeColumns
  if (options.pivotTableType === 'TimeTracking') {
    timeColumns = options.timeTrackingColumns
  } else if (options.pivotTableType === 'TimeBalance') {
    timeColumns = options.timeBalanceColumns
  }
  var exportColumns = options.configOptions.exportColumns
  var timeFormat = options.configOptions.durationTypeForExport || 'h'
  if (timeFormat === 'd') {
    options.periodDuration = 3600 * parseFloat(options.jiraConfig.workingHoursPerDay || 8)
  } else if (timeFormat === 'm') {
    options.periodDuration = 60
  } else {
    options.periodDuration = 3600
  }
  var issueColumnsPart = ['project', 'issuetype', 'key', 'summary', 'priority']
    .filter(function (column) {
      return exportColumns.indexOf(column) >= 0
    })
  var excel = '<style>\n'
  excel += '<!--br\n'
  excel += '    {mso-data-placement:same-cell;}\n'
  excel += '<!--table\n'
  var separators = (1234.5).toLocaleString().match(/\d+(\D)?\d+(\D)\d+/)
  excel += '    {mso-displayed-decimal-separator:"\\' + separators[2] + '";'
  excel += (separators[1] ? '\n    mso-displayed-thousand-separator:"\\' + separators[1] + '";' : '') + '}\n'
  excel += '-->\n'
  excel += '</style>\n'
  excel += '<table>\n'
  excel += '  <tr>\n'
  excel += issueColumnsPart
    .map(function (column) {
      return '    <td>' + options.translations[column] + '</td>\n'
    })
    .join('')
  var exportDescriptionStatus = exportColumns.indexOf('descriptionstatus') >= 0
  if (options.pivotTableType === 'TimeBalance') {
    if (exportColumns.indexOf('datestarted') >= 0) {
      excel += '    <td>' + options.translations['Date Started'] + '</td>\n'
    }
    if (exportColumns.indexOf('displayname') >= 0) {
      excel += '    <td>' + options.translations['Display Name'] + '</td>\n'
    }
    excel += '    <td>' + options.translations['Changed'] + '</td>\n'
  }
  var exportWorkCreated = exportColumns.indexOf('workcreated') >= 0
  for (var i = 0; i < options.moreFields.length; i++) {
    var moreField = options.moreFields[i]
    if (['timeestimate', 'timeoriginalestimate', 'timespent'].indexOf(moreField.id) >= 0) {
      excel += '    <td>' + moreField.name + ' (' + timeFormat + ')</td>\n'
    } else {
      excel += '    <td>' + this.enquote(moreField.name) + '</td>\n'
    }
    if (moreField.id === 'workcreated') {
      exportWorkCreated = false
    }
  }
  if (isTimeView) {
    for (var i in timeColumns) {
      if (timeColumns[i] === '6progress') {
        excel += '    <td>' + options.translations[timeColumns[i]] + '</td>\n'
      } else {
        excel += '    <td>' + options.translations[timeColumns[i]] + ' (' + timeFormat + ')</td>\n'
      }
    }
  } else {
    if (exportWorkCreated) {
      excel += '    <td>' + options.translations['Work Created'] + '</td>\n'
    }
    if (exportColumns.indexOf('worklogid') >= 0) {
      excel += '    <td>' + options.translations['Worklog Id'] + '</td>\n'
    }
    if (exportColumns.indexOf('datestarted') >= 0) {
      excel += '    <td>' + options.translations['Date Started'] + '</td>\n'
    }
    if (['IssuePassedTimeByStatus', 'IssuePassedTimeByResolution'].indexOf(options.pivotTableType) >= 0) {
      excel += '    <td>' + options.translations['Date Ended'] + '</td>\n'
    }
    if (exportColumns.indexOf('displayname') >= 0) {
      excel += '    <td>' + options.translations['Display Name'] + '</td>\n'
    }
    if (exportColumns.indexOf('emailaddress') >= 0) {
      excel += '    <td>' + options.translations['Email Address'] + '</td>\n'
    }
    if (exportColumns.indexOf('accountid') >= 0) {
      excel += '    <td>' + options.translations['Account Id'] + '</td>\n'
    }
    excel += '    <td>' + options.translations['Time Spent'] + ' (' + timeFormat + ')</td>\n'
    if (exportDescriptionStatus) {
      if (options.pivotTableType === 'IssuePassedTimeByStatus') {
        excel += '    <td>' + options.translations['Status'] + '</td>\n'
      } else if (options.pivotTableType === 'IssuePassedTimeByResolution') {
        excel += '    <td>' + options.translations['Resolution'] + '</td>\n'
      } else {
        excel += '    <td>' + options.translations['Work Description'] + '</td>\n'
      }
    }
  }
  excel += '  </tr>\n'
  var totalTimeSpentSeconds = 0
  for (var i in this.allIssues) {
    var pivotEntries = pivotTable.add(this.allIssues[i])
    for (var i in pivotEntries) {
      excel += '  <tr>\n'
      var pivotEntry = pivotEntries[i]
      // rowKey is PivotKey.Issue
      var issue = pivotEntry.rowKey._issue || pivotEntry.rowKey.issue
      var worklog = pivotEntry.rowKey.worklog || pivotEntry.worklog || {} // for PassedTimeByStatus & TimeBalance
      worklog.comment = worklog.comment ? worklog.comment.replace(/<([^>]+)>/g, '&lt;$1&gt;') : ''
      if (exportColumns.indexOf('project') >= 0) {
        excel += '    <td>' + this.enquote(issue.fields.project.name) + '</td>\n'
      }
      if (exportColumns.indexOf('issuetype') >= 0) {
        excel += '    <td>' + this.enquote(issue.fields.issuetype.name) + '</td>\n'
      }
      if (exportColumns.indexOf('key') >= 0) {
        excel += '    <td>' + prepareLink('/browse/' + issue.key, issue.key) + '</td>\n'
      }
      if (exportColumns.indexOf('summary') >= 0) {
        excel += '    <td>' + prepareLink('/browse/' + issue.key, issue.fields.summary) + '</td>\n'
      }
      if (exportColumns.indexOf('priority') >= 0) {
        excel += '    <td>' + this.enquote(this.fieldValue(issue, 'priority', options)) + '</td>\n'
      }
      if (options.pivotTableType === 'TimeBalance') {
        if (exportColumns.indexOf('datestarted') >= 0) {
          excel += '    <td>' + (worklog.started ? moment(worklog.started).format(TimesheetUtils.dateTimeFormat) : '') + '</td>\n'
        }
        if (exportColumns.indexOf('displayname') >= 0) {
          excel += '    <td>' + (worklog.author ? this.enquote(worklog.author.displayName) : '') + '</td>\n'
        }
        excel += '    <td>' + this.enquote(worklog.comment) + '</td>\n'
      }
      for (var i = 0; i < options.moreFields.length; i++) {
        var moreField = options.moreFields[i]
        excel += '    <td>' + this.enquote(this.fieldValue(issue, moreField, options, worklog)) + '</td>\n'
      }
      if (isTimeView) {
        for (var i in timeColumns) {
          if (timeColumns[i] === '6progress') {
            // see timeTracking filter
            var value = pivotEntry.values['6progress']
            if (value.timespent + value.estimate === 0) {
              excel += '    <td></td>\n'
            } else {
              var percents = Math.round(value.timespent * 100 / (value.estimate + value.timespent)) // calcColumnFunction(row);
              excel += '    <td>' + DrawHelper.formatDecimal(percents, maxFractionDigits) + '%</td>\n'
            }
          } else if (timeColumns[i] === '5originalestimateremaining') {
            excel += '    <td>' + DrawHelper.formatDecimal(pivotEntry.values[timeColumns[i]].value / options.periodDuration, maxFractionDigits) + '</td>\n'
          } else {
            excel += '    <td>' + DrawHelper.formatDecimal(pivotEntry.values[timeColumns[i]] / options.periodDuration, maxFractionDigits) + '</td>\n'
          }
        }
      } else {
        if (exportWorkCreated) {
          excel += '    <td>' + (worklog.created ? moment(worklog.created).format(TimesheetUtils.dateTimeFormat) : '') + '</td>\n'
        }
        if (exportColumns.indexOf('worklogid') >= 0) {
          excel += '    <td>' + (worklog.id || '') + '</td>\n'
        }
        if (exportColumns.indexOf('datestarted') >= 0) {
          excel += '    <td>' + (worklog.workDate ? moment(worklog.workDate).format(TimesheetUtils.dateTimeFormat) : '') + '</td>\n'
        }
        if (['IssuePassedTimeByStatus', 'IssuePassedTimeByResolution'].indexOf(options.pivotTableType) >= 0) {
          excel += '    <td>' + (worklog.toDate ? moment(worklog.toDate).format(TimesheetUtils.dateTimeFormat) : '') + '</td>\n'
        }
        if (exportColumns.indexOf('displayname') >= 0) {
          excel += '    <td>' + (worklog.author ? this.enquote(worklog.author.displayName) : '') + '</td>\n'
        }
        if (exportColumns.indexOf('emailaddress') >= 0) {
          excel += '    <td>' + (worklog.author ? this.enquote(worklog.author.emailAddress) : '') + '</td>\n'
        }
        if (exportColumns.indexOf('accountid') >= 0) {
          excel += '    <td>' + (worklog.author ? worklog.author.accountId : '') + '</td>\n'
        }
        excel += '    <td>' + (worklog.timeSpentSeconds ? DrawHelper.formatDecimal(worklog.timeSpentSeconds / options.periodDuration, maxFractionDigits) : '') + '</td>\n'
        if (exportDescriptionStatus) {
          excel += '    <td>' + (worklog._issue ? prepareLink('/browse/' + worklog._issue.key, worklog._issue.key) + ' ' : '') +
                        this.enquote(['IssuePassedTimeByStatus', 'IssuePassedTimeByResolution'].indexOf(options.pivotTableType) >= 0 ? pivotEntry.columnKey.keyValue : worklog.comment) + '</td>\n'
        }
        excel += '  </tr>\n'
        totalTimeSpentSeconds += worklog.timeSpentSeconds || 0
      }
    }
  }
  var colspanForTotals = exportColumns.length // field after totals
  if (exportDescriptionStatus) {
    colspanForTotals--
  }
  if (exportColumns.indexOf('timespentd') >= 0) {
    colspanForTotals--
  }
  if (exportColumns.indexOf('workcreated') >= 0 && !exportWorkCreated) {
    colspanForTotals--
  }
  excel += '  <tr>\n'
  var moreFieldsLength = options.moreFields.length
  var colspan = isTimeView ? issueColumnsPart.length : colspanForTotals
  if (colspan > 0) {
    colspan--
    excel += '    <td>Total</td>\n'
  }
  if (options.pivotTableType === 'TimeBalance') {
    if (exportColumns.indexOf('datestarted') >= 0) {
      colspan++
    }
    if (exportColumns.indexOf('displayname') >= 0) {
      colspan++
    }
    colspan++
  }
  excel += TimesheetUtils.repeat('    <td></td>\n', colspan + moreFieldsLength)
  if (['IssuePassedTimeByStatus', 'IssuePassedTimeByResolution'].indexOf(options.pivotTableType) >= 0) {
    excel += '    <td></td>\n'
  }
  if (isTimeView) {
    for (var i in timeColumns) {
      if (timeColumns[i] === '6progress') {
        // see timeTracking filter
        var value = pivotTable.totals['6progress'].value
        if (value.timespent + value.estimate === 0) {
          excel += '    <td></td>\n'
        } else {
          var percents = Math.round(value.timespent * 100 / (value.estimate + value.timespent)) // calcColumnFunction(row);
          excel += '    <td>' + DrawHelper.formatDecimal(percents, maxFractionDigits) + '%</td>\n'
        }
      } else if (timeColumns[i] === '5originalestimateremaining') {
        excel += '    <td>' + DrawHelper.formatDecimal(pivotTable.totals[timeColumns[i]].value.value / options.periodDuration, maxFractionDigits) + '</td>\n'
      } else {
        excel += '    <td>' + DrawHelper.formatDecimal(pivotTable.totals[timeColumns[i]].sum / options.periodDuration, maxFractionDigits) + '</td>\n'
      }
    }
  } else {
    excel += '    <td>' + DrawHelper.formatDecimal(totalTimeSpentSeconds / options.periodDuration, maxFractionDigits) + '</td>\n'
    if (exportDescriptionStatus) {
      excel += '    <td></td>\n'
    }
  }
  excel += '  </tr>\n'
  var restrictedProjects = options.restrictedProjects && options.restrictedProjects.join(',')
  if (restrictedProjects) {
    excel += "<tr><td class='nav border total' colspan=" + colspan + '>' + options.translations['Data is limited by Auditors Roles'] + ' ' + restrictedProjects + '</td></tr>'
  }
  excel += '</table>\n'
  return excel
}
ExcelView.prototype.download = function (options) {
  var excel = this.generate(options)
  var excelFile = '<html xmlns:o="urn:schemas-microsoft-com:office:office" xmlns:x="urn:schemas-microsoft-com:office:excel" xmlns="http://www.w3.org/TR/REC-html40">\n'
  excelFile += '<meta http-equiv="content-type" content="application/vnd.ms-excel; charset=utf-8">'
  excelFile += '<head>\n'
  excelFile += '<!--[if gte mso 9]>\n'
  excelFile += '<xml>\n'
  excelFile += '<x:ExcelWorkbook>'
  excelFile += '<x:ExcelWorksheets>'
  excelFile += '<x:ExcelWorksheet>'
  excelFile += '<x:Name>'
  excelFile += '{worksheet}'
  excelFile += '</x:Name>'
  excelFile += '<x:WorksheetOptions>'
  excelFile += '<x:DisplayGridlines/>'
  excelFile += '</x:WorksheetOptions>'
  excelFile += '</x:ExcelWorksheet>'
  excelFile += '</x:ExcelWorksheets>'
  excelFile += '</x:ExcelWorkbook>\n'
  excelFile += '</xml>\n'
  excelFile += '<![endif]-->\n'
  excelFile += '</head>\n'
  excelFile += '<body>\n'
  excelFile += excel
  excelFile += '</body>\n'
  excelFile += '</html>\n'

  var fileName = options.fileName ? options.fileName : 'exportData.xls'
  var fileData = [excelFile]
  var blobObject = new Blob(fileData, { encoding: 'utf-8', type: 'application/vnd.ms-excel;charset=utf-8;' })
  var isIE = /* @cc_on!@ */false || !!document.documentMode // At least IE6
  if (isIE) {
    window.navigator.msSaveOrOpenBlob(blobObject, fileName)
  } else {
    var data
    if (navigator.userAgent.indexOf('Safari') >= 0 && navigator.userAgent.indexOf('Chrome') < 0) {
      var base64data = 'base64,' + AJS.$.base64.encode(excelFile)
      data = 'data:application/vnd.ms-excel;charset=utf-8;filename=' + fileName + ';' + base64data
    } else {
      data = URL.createObjectURL(blobObject)
    }
    var link = document.createElement('a')
    if (typeof link.download === 'string') {
      link.href = data
      link.download = fileName
      // Firefox requires the link to be in the body
      document.body.appendChild(link)
      link.click()
      setTimeout(function () {
        document.body.removeChild(link)
      }, 100)
    } else {
      window.open(data)
    }
  }
}

if (typeof module === 'object' && module.exports) {
  module.exports = ExcelView
  var PivotTableFactory = require('./PivotTable')
  var TimesheetUtils = require('../../timesheetUtils')
  var DrawHelper = require('../../app/directives/DrawHelper')
  var moment = require('moment-timezone')
}
