'use strict'
var directives = angular.module('directives', ['talis.services.logging'])

directives.directive('context', ['applicationLoggingService', function (logger) {
  return {
    restrict: 'E',
    scope: {
      items: '='
    },
    link: function (scope) {
      logger.trace('context', scope.items)
      var context = TimesheetUtils.getJson(scope.items)
      logger.baseUrl = context.hostBaseUrl
      angular.extend(scope.$parent, context)
    }
  }
}])

directives.directive('autoFocus', ['$timeout', function ($timeout) {
  return function (scope, element) {
    $timeout(function () {
      element.focus().select()
    }, 0)
  }
}])

directives.directive('formInvalid', [function () {
  return {
    restrict: 'E',
    require: '^form',
    link: function (scope, element, attrs, form) {
      var submitButton = AP.dialog.getButton('submit')
      scope.$watch(form.$name + '.$invalid', function (invalid) {
        if (invalid) {
          submitButton.disable()
        } else {
          submitButton.enable()
        }
      })
      AP.events.on('dialog.submit', function () {
        submitButton.disable()
      })
    }
  }
}])

directives.directive('logWorkPermission', ['pivottableService', function (pivottableService) {
  return {
    restrict: 'A',
    require: 'ngModel',
    link: function (scope, element, attrs, controller) {
      scope.$watch('worklog.issueKey', function (newValue) {
        if (newValue && !scope.worklog.user) {
          pivottableService.havePermissions(['WORK_ON_ISSUES'], 'issueKey=' + newValue).then(function (permissions) {
            controller.$setValidity(element[0].name, permissions[0])
          })
        }
      })
    }
  }
}])

// validate timepsent string, e.g. 1w 2d 3h 4m, or 1 (default time unit)
// see also timesheetUtils.getTimeSpentMs()
directives.directive('timespent', [function () {
  return {
    require: 'ngModel',
    scope: {
      zeroable: '='
    },
    link: function (scope, elm, attrs, ctrl) {
      ctrl.$validators.timespent = function (modelValue, viewValue) {
        if (!viewValue || /^\d*\.?\d+$/.test(viewValue)) {
          return !viewValue || parseFloat(viewValue) > 0
        }
        var blocks = viewValue.split(' ')
        var units = ['w', 'd', 'h', 'm']
        var result = blocks.length > 0
        for (var i = 0; i < blocks.length && result && units.length > 0; i++) {
          do {
            var unit = units.shift()
            var r = RegExp('^(\\d*\\.?\\d+)' + unit + '$')
            var m = blocks[i].match(r)
            result = m && (parseFloat(m[1]) > 0 || scope.zeroable && parseFloat(m[1]) === 0)
          } while (units.length > 0 && !result)
        }
        return result
      }
    }
  }
}])

directives.directive('longerOneDayWarning', [function () {
  return {
    restrict: 'E',
    scope: true,
    controller: ['$scope', function ($scope) {
      $scope.warning = {
        show: false
      }
      $scope.$watch(($scope.popupData ? 'popupData.' : '') + 'worklog.timeSpent', function (timespent) {
        $scope.warning.show = TimesheetUtils.isLongerOneDay(timespent, $scope.jiraConfig)
      })
    }],
    transclude: true,
    template: '<span class="yellow" data-ng-show="warning.show" ng-transclude></span>'
  }
}])

directives.directive('error', [function () {
  return {
    restrict: 'E',
    link: function (scope, element) {
      $(element).click(function () {
        scope.$apply(function () {
          throw new Error('Test')
        })
      })
    }
  }
}])
